.navbar {
  background-color: var(--lavender);
  height: 10vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  width: 100vw;
  box-shadow: 0 0 12px var(--box-shadow);
  z-index: 2;
}
.navbar img {
  margin: 0 1rem;
}

.navbar ul {
  display: flex;

  justify-content: center;
  align-items: center;
  padding: 0;
  gap: 1.5rem;
}

.navbar button {
  border: none;
  background-color: transparent;
  margin: 0 1rem;
  cursor: pointer;
  border-radius: 50px;
  transition: 0.2s ease;
}

.navbar button:hover {
  background-color: var(--lavender-hover);
}

.navbar svg {
  width: 32px;
  height: 32px;
}

.navbar div {
  display: flex;
}

.navbar a {
  font-weight: bold;
}

.navbar a:hover {
  color: var(--myrtle-green);
  border-bottom: 2px solid var(--myrtle-green);
}

.overlay {
  position: fixed;
  z-index: 1;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: right;
}

.cart {
  background-color: var(--white);
  width: 17rem;
  animation: growing 0.6s 0.3s forwards;
  height: 5rem;
  border-radius: 10px;
  box-shadow: 0px 0px 12px var(--box-shadow);
  max-height: 17rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0;
  margin-top: 13vh;
  margin-right: 2rem;
  min-height: 3rem;
}

.cart p {
  border-bottom: 2px solid var(--platinum);
  width: 100%;
  height: 20px;
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 5px;
  color: var(--platinum);
}

@keyframes growing {
  from {
    height: 5rem;
  }
  to {
    height: 17rem;
  }
}

@media (max-width: 500px) {
  .navbar div {
    flex-direction: row-reverse;
  }

  .overlay {
    background-color: var(--overlay);
  }

  .navbar svg {
    width: 42px;
    height: 42px;
  }

  .menu {
    background-color: var(--lavender);
    margin: 0;
    height: 100vh;
    width: 40vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 2rem;
    font-weight: bold;
    animation: sliding 0.2s;
  }

  @keyframes sliding {
    from {
      transform: translateX(300px);
    }
    to {
      transform: translateX(0);
    }
  }

  .cart {
    width: 100%;
    animation: growing 0.5s;
    height: 17rem;
    border-radius: 10px;
    box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.209);
    display: flex;
    justify-content: center;
    padding: 0;
    margin: 0;
    margin-top:10vh;
  }

  @keyframes growing {
    from {
      height: 2rem;
    }
    to {
      height: 17rem;
    }
  }
}
